*, :before, :after {
  border: 0;
  padding: 0;
}

article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizespeed;
  font-family: Plus Jakarta Sans, sans-serif;
  font-size: 16px;
}

html {
  min-height: 100%;
  position: relative;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0 !important;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p, ul {
  font-weight: inherit;
  font-size: inherit;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
  line-height: 1;
}

a {
  text-decoration: none !important;
}

ol, ul, li {
  list-style: none;
}

input, button, textarea, select {
  font: inherit;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

